@font-face {
  font-family: "Manrope";
  src: url("../fonts/Manrope-Regular.woff2") format("woff2"), url("../fonts/Manrope-Regular.woff") format("woff");
  font-style: regular;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Manrope";
  src: url("../fonts/Manrope-SemiBold.woff2") format("woff2"), url("../fonts/Manrope-SemiBold.woff") format("woff");
  font-style: bold;
  font-weight: 700;
  font-display: swap;
}
@font-face {
  font-family: "AnonymousPro";
  src: url("../fonts/AnonymousPro-Regular.woff2") format("woff2"), url("../fonts/AnonymousPro-Regular_1.woff") format("woff");
  font-style: regular;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "AnonymousPro";
  src: url("../fonts/AnonymousPro-Bold.woff2") format("woff2"), url("../fonts/AnonymousPro-Bold.woff") format("woff");
  font-style: bold;
  font-weight: 700;
  font-display: swap;
}
.page-body {
  font-family: "AnonymousPro", "Arial", sans-serif;
  font-size: 16px;
  line-height: 130%;
  background-color: #f1f1f1;
  max-width: 1440px;
  margin: 0 auto;
  height: 100vh;
  color: #232B2A;
  display: grid;
  grid-template-areas: "header header aside" "section section aside" "footer footer footer";
  grid-template-rows: 80px 1fr 100px;
  grid-template-columns: 0 1fr 30%;
  height: 100vh;
}

@media (max-width: 900px) {
  .page-body {
    grid-template-areas: "header" "section" "aside" "footer";
    grid-template-rows: 50px 420px 1fr 80px;
    grid-template-columns: 100%;
    height: auto;
  }
}
@media (max-width: 450px) {
  .page-body {
    grid-template-rows: 50px 530px 1fr 200px;
    grid-template-columns: 100%;
    height: auto;
  }
}
.title {
  font-weight: 400;
  text-transform: uppercase;
}

.link {
  text-decoration: none;
  color: #f1f1f1;
  transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out, border-color 0.2s ease-in-out;
}
.link:hover {
  color: #232B2A;
}
.link_dark {
  color: #232B2A;
}
.link_dark:hover {
  color: #81b3d2;
}

.button {
  display: block;
  background-color: #81b3d2;
  color: #f1f1f1;
  padding: 16px 32px;
  text-decoration: none;
  white-space: nowrap;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 20px;
  border: none;
  max-width: 260px;
  text-align: center;
  border-radius: 4px;
  z-index: 20;
}
.button:hover {
  background-color: #6C99B5;
}
.button:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
.button:disabled {
  background-color: #e5e5e5;
}

.page-header {
  background-color: #f1f1f1;
  grid-area: header;
  padding: 0 30px;
  padding-right: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 4px;
  margin: 10px 0;
}
.page-header__logo, .page-header__lang {
  color: #81b3d2;
  margin: 0;
  font-size: 20px;
  line-height: 20px;
  font-weight: 700;
}

@media (max-width: 450px) {
  .page-header {
    padding-right: 30px;
  }
}
.main-section {
  padding: 30px;
  padding-top: 20px;
  background: #f1f1f1;
  grid-area: section;
  position: relative;
}
.main-section__img {
  position: absolute;
  bottom: -95px;
  right: -22%;
  pointer-events: none;
}
.main-section__title {
  font-size: 80px;
  line-height: 80px;
  display: inline-block;
  max-width: 900px;
  margin: 0;
  margin-bottom: 20px;
}
.main-section__description {
  display: block;
  max-width: 500px;
  margin: 0;
  margin-bottom: 40px;
  line-height: 120%;
}

.blue {
  color: #81b3d2;
}

@media (max-width: 1365px) {
  .main-section__img {
    display: none;
  }
}
@media (max-width: 900px) {
  .main-section__title {
    font-size: 50px;
    line-height: 55px;
    max-width: 500px;
  }
  .main-section__description {
    margin: 0 auto;
    margin-bottom: 30px;
  }
}
.aside {
  background-color: #81b3d2;
  grid-area: aside;
  overflow-y: scroll;
  border-radius: 4px;
  margin: 10px;
  margin-bottom: 0;
  z-index: 2;
  padding: 20px;
}
.aside__list {
  display: block;
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.aside__title {
  display: inline-block;
  margin: 0;
  margin-bottom: 15px;
  color: #f1f1f1;
  font-size: 20px;
  line-height: 20px;
  font-weight: 700;
}
.aside__item {
  list-style-type: none;
  display: block;
  padding: 20px;
  background-color: #f1f1f1;
  margin-bottom: 10px;
  border-radius: 4px;
}
.aside__item-title {
  margin: 0;
  margin-bottom: 10px;
}
.aside__description {
  margin: 0;
}
.aside__number {
  margin: 0;
  margin-bottom: 10px;
  color: #81b3d2;
}

@media (max-width: 900px) {
  .aside {
    overflow-y: hidden;
    overflow-x: scroll;
  }
  .aside__list {
    display: flex;
  }
  .aside__item {
    margin-right: 10px;
    min-width: 300px;
  }
}
.main-footer {
  background: #81b3d2;
  grid-area: footer;
  z-index: 2;
  border-radius: 4px;
  margin: 0 10px;
  padding: 0 30px;
  padding-right: 20px;
  color: #f1f1f1;
  font-size: 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.main-footer__copyright, .main-footer__title {
  margin: 0;
  margin-right: 20px;
}
.main-footer__contact-wrapper {
  display: flex;
}
.main-footer__contacts {
  list-style-type: none;
  display: flex;
  margin: 0;
  padding: 0;
}
.main-footer__item:not(:last-child) {
  margin-right: 20px;
}

@media (max-width: 450px) {
  .main-footer {
    flex-direction: column-reverse;
    align-items: flex-start;
    padding: 20px;
    margin: 10px;
  }
  .main-footer__title {
    margin-bottom: 10px;
    font-size: 20px;
    line-height: 20px;
    font-weight: 700;
  }
  .main-footer__contact-wrapper {
    flex-direction: column;
  }
}
.bg {
  position: fixed;
  pointer-events: none;
  top: -50%;
  left: -50%;
  right: -50%;
  bottom: -50%;
  width: 200%;
  height: 200vh;
  background: transparent url("../../img/noise-transparent.png") repeat 0 0;
  background-repeat: repeat;
  animation: bg-animation 0.3s infinite;
  opacity: 0.9;
  visibility: visible;
  z-index: 2;
}

@keyframes bg-animation {
  0% {
    transform: translate(0, 0);
  }
  10% {
    transform: translate(-5%, -5%);
  }
  20% {
    transform: translate(-10%, 5%);
  }
  30% {
    transform: translate(5%, -10%);
  }
  40% {
    transform: translate(-5%, 15%);
  }
  50% {
    transform: translate(-10%, 5%);
  }
  60% {
    transform: translate(15%, 0);
  }
  70% {
    transform: translate(0, 10%);
  }
  80% {
    transform: translate(-15%, 0);
  }
  90% {
    transform: translate(10%, 5%);
  }
  100% {
    transform: translate(5%, 0);
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.hidden {
  display: none;
}

.visible-on-mobile {
  display: inline;
}
@media (min-width: 768px) {
  .visible-on-mobile {
    display: none;
  }
}
@media (min-width: 1200px) {
  .visible-on-mobile {
    display: none;
  }
}

.visible-on-tablet {
  display: none;
}
@media (min-width: 768px) {
  .visible-on-tablet {
    display: inline;
  }
}
@media (min-width: 1200px) {
  .visible-on-tablet {
    display: none;
  }
}

.visible-on-desktop {
  display: none;
}
@media (min-width: 768px) {
  .visible-on-desktop {
    display: none;
  }
}
@media (min-width: 1200px) {
  .visible-on-desktop {
    display: inline;
  }
}

.hidden-on-mobile {
  display: inline;
}
@media (min-width: 320px) and (max-width: 767px) {
  .hidden-on-mobile {
    display: none;
  }
}

.hidden-on-tablet {
  display: inline;
}
@media (min-width: 768px) and (max-width: 1199px) {
  .hidden-on-tablet {
    display: none;
  }
}